import React, { useState } from "react";
import axios from "axios";

const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("user"); // Default to user
  const [errorMessage, setErrorMessage] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Determine the API endpoint based on the role
    const endpoint =
      role === "admin"
        ? "http://localhost:8800/api/auth/adminLogin"
        : "http://localhost:8800/api/auth/login";

    try {
      // Send the login request with the role
      const response = await axios.post(endpoint, {
        username,
        password,
      });

      // Handle success - You can save the JWT token in localStorage/sessionStorage
      localStorage.setItem("token", response.data.token);
      console.log("Login successful");
    } catch (error) {
      setErrorMessage("Invalid username, password, or role");
    }
  };

  return (
    <div className="bg-black p-6 rounded-md w-96 mx-auto mt-10">
      <h2 className="text-white text-xl mb-4">Login</h2>
      {errorMessage && <p className="text-red-500">{errorMessage}</p>}
      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label className="text-white">Username</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-full p-2 mt-2 border border-gray-300 rounded"
            required
          />
        </div>
        <div className="mb-4">
          <label className="text-white">Password</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full p-2 mt-2 border border-gray-300 rounded"
            required
          />
        </div>
        <div className="mb-4">
          <label className="text-white">Role</label>
          <select
            value={role}
            onChange={(e) => setRole(e.target.value)}
            className="w-full p-2 mt-2 border border-gray-300 rounded"
          >
            <option value="user">User</option>
            <option value="admin">Admin</option>
          </select>
        </div>
        <button
          type="submit"
          className="w-full bg-blue-500 text-white p-2 rounded-md mt-4"
        >
          Login
        </button>
      </form>
    </div>
  );
};

export default Login;
